import { createStore } from "vuex";

const store = createStore({
    state: {
        users: [
            { name: "Tursunxon", isMarried: false },
            { name: "Temur", isMarried: true },
            { name: "Alisher", isMarried: true },
        ],
        product: null

    },
    mutations: {
        setUser(state, payload) {
            state.users.push(payload)
        },
        getProduct(state,payload){
            state.product = payload
        }
    },
    actions: {
        setUser({ commit }) {
            setTimeout(() => {
                const newUser = { name: "G'aybulla", isMarried: false }
                commit('setUser', newUser)
            }, 1000);
        },
        async getProduct({commit}) {
            let res = await fetch('https://dummyjson.com/products')
            let json = await res.json()
            let arr = json.products
            commit('getProduct',arr)
        }
    },
    getters: {
        getMarried(state) {
            return state.users.filter(user => user.isMarried)
        }
    }
})
export default store