<template>
  <div class="product container">
    <div v-for="item in product" :key="item.id" class="card">
      <img :src="item.thumbnail" alt="" class="card__img" />
      <div class="card__info">
        <h2 class="card__brand">Brand:{{ item.brand }}</h2>
        <h2 class="card__title">Name:{{ item.title }}</h2>
        <p class="card__text">{{ item.description }}</p>
        <h2 class="card__stock">stock:{{ item.stock }}</h2>
        <h2 class="card__price">price:{{ item.price }}$</h2>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapState } from "vuex";  
export default {
  computed: {
    ...mapState(["product"]),
  },
  methods: {
    ...mapActions(["getProduct"]),
  },
  mounted() {
    this.getProduct();
  },
};
</script>
<style lang=""></style>
